import React from 'react';
import { useHistory,useParams } from 'react-router-dom';
import { Form } from "../components";
import { FooterContainer } from '../containers/footer';
import * as ROUTES from '../constants/routes';
import { useForm } from "react-hook-form";
import {
  signinRequest,
  signinSuccess,
  signinFail,
} from '../reducers/slices/userLoginSlice';
import { useDispatch, useSelector } from 'react-redux';
import {auth} from '../lib/firebase.prod';
import { Input } from '../components/form/styles/form'; 
import { ImSpinner } from 'react-icons/im';
import { SignInHeaderContainer } from '../containers/Form/SignInHeader';
 
import "./css/login.css";
 

export default function  SignIn() {
  //     
  const history = useHistory();

  const dispatch = useDispatch();
  const { loading, error } = useSelector((state) => state.userLogin);
  const { email } = useParams();

  //  Form 
  const {register,handleSubmit,formState: {errors},} = useForm();

   //   handle async
  const handleSubmitonClick = async(data) => {
    // handleClick  dispatch  
    dispatch(signinRequest());
    try {
      //    firbase data email password 
        const response = await auth.signInWithEmailAndPassword(
              data.email,
              data.password
        );
 
        //  dispatch  Brose    
        dispatch(signinSuccess(JSON.stringify(response.user)));
          history.push(ROUTES.BROWSE);

      } catch(error) {
          
        //   
            dispatch(signinFail(error.message));
    
      }
  }; 

   
 
    //   
    return (
       <>
         <SignInHeaderContainer>
            <Form login>
              <Form.Title></Form.Title>
              <Form.Base onSubmit={handleSubmit(handleSubmitonClick)}>  
               {error && <Form.Error>.        .     .</Form.Error>}
                 <Input 
                    type="text"
                    {...register('email', {
                      required: '',
                      //    
                      pattern: {
                        //   
                        value: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i,
                        message: '    ',
                      }
                    })}
                    id='email'
                    defaultValue={email ? email: ''}
                    placeholder=' '
                />
                {/*     */}
                {errors.email && <span>{errors.email.message}</span>}

               {/*     4~10      */}
                <Input 
                  type="password"
                   {...register('password', {
                     required: 
                       '  4~10   ..',
                      
                       minLength: {
                        value: 4,
                        message:
                            '  4-10   .',
                    },
                    //    15    
                    maxLength: {
                        value: 15,
                        message:
                            '    .',
                    },
                   //  
                    wrongPassword: {
                        value:'password',
                        message: '  .'
                    }
                   })}
                   id='password'
                   placeholder=""
                />
                 {errors.password && <span>{errors.password.message}</span>}
            
              {/*    lodaing     */}
              {loading ? (
                <Form.Submit>
                    <ImSpinner/>
                  </Form.Submit>
              ): (
                <Form.Submit 
                type="submit" 
                data-testid="sign-in" >
                    
                </Form.Submit>
           )}
              </Form.Base>
 
                {/*    ==    */}
              <div className="check-container">
                <label className="login-check" htmlFor="login-check">
                    <input id="login-check" type="checkbox"/>
                    <span>  </span>
                  </label>  
                    <Form.Link to={ROUTES.LOGIN_HELP}> ?</Form.Link>
              </div>
           
              <Form.Text>
                {/*   ROUTES  SIGNUP  */}
                Netflix  ? <Form.Link to={ROUTES.SIGN_UP}> </Form.Link>
              </Form.Text>

              {/*  Toggle    */}
              <Form.Text>
                    Google reCAPTCHA      . <Form.AboutLink href=""> </Form.AboutLink>
              </Form.Text>
            </Form>
         </SignInHeaderContainer>
         <FooterContainer signin />
       </>  
    )
}