/* eslint-disable no-restricted-globals */
import React, {useState, useContext, useEffect} from 'react'
import Fuse from "fuse.js";
import {FirebaseContext } from '../context/firebase';
import { SelectProfileContainer } from './profiles';
import { Header, Loading, Banner } from '../components';
// import { useAuthListener } from 'hooks';
import logo from '../logo.svg';
import * as ROUTES from '../constants/routes';
import { FooterContainer } from './footer';
 
import { useHistory } from 'react-router-dom';
 
// import Banner from '.';

 

export function BrowseContainer({ slides }) {
  const { firebase } = useContext(FirebaseContext);
  const user = firebase.auth().currentUser || {};
  
  //  ==    
  const [profile, setProfile] = useState([]);
  const [loading,setLoading] = useState(true);
  
  //   
  const [searchTerm, setSearchTerm] = useState('');
  const [category,setCategory] = useState('series');
  const [slideRows, setSlideRows] = useState([]);
  
  // 
 
 

  const history = useHistory();
 
 
 
    useEffect(() => {
        setTimeout(() => {
            setLoading(false)
        }, 3000)
    }, [profile.displayName]);
  
     useEffect(() => {
         setSlideRows(slides[category]);
     }, [slides, category]);

      
 
    
    
   

     useEffect(() => {
         const fuse = new Fuse(slideRows, { keys: ['data.description','data.title', 'data.genre'] });
        const results = fuse.search(searchTerm).map(({ item }) => item);

        if(slideRows.length > 0 && searchTerm.length > 3 && results.length > 0) {
            setSlideRows(results);
        } else {
            setSlideRows(slides[category]);
        }
    // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [searchTerm]);


    return profile.displayName ? (
        <>
            {loading ? <Loading src={user.photoURL} /> : <Loading.ReleaseBody /> }
          
          <Header src="joker">
                <Header.Frame>
                {/*  Nav  */}
                    <Header.Group>
                      <Header.Logo to={ROUTES.HOME} src={logo} alt="Netflix"/>
                      <Header.TextLink to={ROUTES.HOME}>
                      
                       
                      </Header.TextLink>
                         <Header.TextLink
                          onClick={() => history.push(ROUTES.BROWSETV)}
                         >
                          TV       
                        </Header.TextLink>
                        <Header.TextLink 
                          onClick={() => history.push(ROUTES.BROWSEMOVIES)}
                        >
                           
                        </Header.TextLink>
                        <Header.TextLink
                          onClick={() => history.push(ROUTES.BROWSELATEST)}
                        >
                                  
                        </Header.TextLink>
                        <Header.TextLink>
              
                                  
                        </Header.TextLink>
                    </Header.Group>

                    {/*  Nav  */}
                     {/*   */}
                    <Header.Group>
                        <Header.Profile>
                            <Header.Search
                              searchTerm={searchTerm}
                              setSearchTerm={setSearchTerm}
                            />
                            <Header.Picture src={user.photoURL} />
                            <Header.Dropdown>
                                <Header.Group>
                                    <Header.Picture src={user.photoURL} />
                                    <Header.TextLink>{user.displayName}</Header.TextLink>
                                </Header.Group>
                                <Header.Group>
                                    <Header.TextLink onClick={() => firebase.auth().signOut()}> </Header.TextLink>
                                </Header.Group>
                            </Header.Dropdown>
                        </Header.Profile>
                    </Header.Group>
                </Header.Frame>
          </Header>       
      
        {/*   */}

        {/* Rows  */}
         {/*  */}
         <Banner />

        <FooterContainer />      
        </>
    ) : (
      <SelectProfileContainer user={user} setProfile={setProfile} />
    
    )
}