import React, { useState} from 'react';
import { useHistory,useParams } from 'react-router-dom';
 
// import { FooterContainer } from '../containers/footer';
import * as ROUTES from '../constants/routes';
import { useForm } from "react-hook-form";
import {
  signinRequest,
  signinSuccess,
  signinFail,
} from '../reducers/slices/userLoginSlice';
import { useDispatch, useSelector } from 'react-redux';
import {auth} from '../lib/firebase.prod';
import { Input } from '../components/form/styles/form'; 
import { ImSpinner } from 'react-icons/im';
import "./css/login.css";
// import SignInHelpContainer from '../containers/PassWordForm/SignInHeader';
import { LogInHelpContainer } from '../containers/Form/LogInHelpContainer';
import { PassWordFooterContainer } from '../containers/Form/PasswordFooter';
import { PassWordForm } from '../components';
 

export default function  LogInHelp() {
  //     
  const history = useHistory();

  const dispatch = useDispatch();
  const { loading, error } = useSelector((state) => state.userLogin);

  const { email } = useParams();

 
  // 
  const {register,handleSubmit,formState: {errors},} = useForm();

  //   = 
  const handleSubmitonClick = async(data) => {
      dispatch(signinRequest());
      try {
        //   ==  
        const response = await auth.sendPasswordResetEmail(
              data.email,
              data.password,
              
        );
        if (!data.email !== data.email) {
          dispatch(signinFail(error.message));
        }
 
        dispatch(signinSuccess(JSON.stringify(response.user)));
          history.push(ROUTES.BROWSE);
      } catch(error) {
            dispatch(signinFail(error.message));
      } 
  }; 

   
 
    //   
    return (
       <>
         <LogInHelpContainer>
            <PassWordForm login>
              <PassWordForm.Title> </PassWordForm.Title>
              <PassWordForm.Base onSubmit={handleSubmit(handleSubmitonClick)}>  
               {error && <PassWordForm.Error>     .  </PassWordForm.Error>}
               
                 <Input 
                    type="text"
                    {...register('email', {
                      required: '',
                      //    
                      pattern: {
                        //   = 
                        value: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i,
                        message: '    ',
                      }
                    })}
                    id='email'
                    defaultValue={email ? email: ''}
                    placeholder=' '
                />
                {/*     */}
                {errors.email && <span>{errors.email.message}</span>}
                
              {loading ? (
                <PassWordForm.Submit>
                    <ImSpinner/>
                  </PassWordForm.Submit>
              ): (
                <PassWordForm.Submit 
                type="submit" 
                data-testid="sign-in" 
                
                >
                      
                </PassWordForm.Submit>
           )}
              </PassWordForm.Base>
 
             
              <PassWordForm.Text>
                    Google reCAPTCHA      . <PassWordForm.AboutLink href=""> </PassWordForm.AboutLink>
              </PassWordForm.Text>
            </PassWordForm>
         </LogInHelpContainer>
         <PassWordFooterContainer signin />
       </>  
    )
}